import getComponentRootDom from './getComponentRootDom';
import Icon from '@/components/Icon';
import svgConfig from '@/components/Icon/svg/svg.config.js';
import eventBus from '@/eventBus.js';
import getRandom from './getRandom';
import store from '@/store';
import debounce from './debounce.js';

//每一种小方块的数量
const blockNum = 10 * 3;
//所有的小方格类型
const allBlockTypes = Object.keys(svgConfig);
const allBlockTypesObj = allBlockTypes.map((item) => {
  return {
    type: item,
    num: blockNum,
  };
});
//所有小方块信息
const allBlockInfo = {
  blockType: [],
  blockPosition: [],
};
//每一个小方块的唯一标识
let blockFlag = 0;

/**
 *
 * @param {Dom} dom 生成的小方块放在Dom里面
 * @param {Function} callback 生成小方块完毕执行的回调
 */

const blockH = 25; //svg高度一半
const blockW = 25; //svg宽度一半

export default function (targetDom, callback) {
  //获取Dom的宽高
  const DomH = targetDom.clientHeight - 20;
  const DomW = targetDom.clientWidth - 20;
  // 宽高均分的等份数
  const eachSizeH = DomH / blockH;
  const eachSizeW = DomW / blockW;
  // 每隔一段时间创建一个小方块
  store.commit('SETLOADINGSTATUS', 'loading');
  let timer = setInterval(() => {
    if (createBlock(targetDom, { eachSizeW, eachSizeH })) {
      //创建完毕，停止setInterval
      console.log('创建小方块完毕!');
      store.commit('SETLOADINGSTATUS', 'end');
      clearInterval(timer);
    }
  }, 10);

  callback && callback();
}

//创建一个小方块
function createBlock(targetDom, opt) {
  if (allBlockTypesObj.length <= 0) {
    return true;
  }
  blockFlag = blockFlag + 1;
  const blockType = getBlockType();
  const dom = getComponentRootDom(Icon, { type: blockType.type });
  const position = getBlockPosition(opt);
  //将每一个小方块信息保存
  allBlockInfo.blockType.push(blockType);
  allBlockInfo.blockPosition.push(position);

  //给每一个小方块绑定事件
  (function (position, blockFlag, blockType) {
    dom.addEventListener('click', function () {
      // 提示用户赶紧游戏
      newHandlePromptUser();

      if (clickEnabled(position, blockFlag)) {
        //可以点击
        console.log('可以点击,移除该小方块');
        const blockNum = allBlockInfo.blockPosition.filter(
          (item) => item !== ''
        );
        // 抛出全局事件
        eventBus.$emit('blockClick', { type: blockType.type, blockNum });

        allBlockInfo.blockType.splice(blockFlag - 1, 1, '');
        allBlockInfo.blockPosition.splice(blockFlag - 1, 1, '');
        this.remove();
      } else {
        // 不能点击
        if (this.classList.contains('active')) {
          this.classList.remove('active');
          this.clientHeight;
          this.classList.add('active');
        } else {
          this.classList.add('active');
        }
        console.log('不能点击');
      }
    });
  })(position, blockFlag, blockType);

  //设置dom的自定义属性
  dom.dataset['index'] = position.left + '|' + position.top + '|' + blockFlag;
  dom.style.position = 'absolute';
  dom.style.top = position.top + 'px';
  dom.style.left = position.left + 'px';
  targetDom.appendChild(dom);
}

//随机获取小方块位置
function getBlockPosition(opt) {
  const randomX = getRandom(0, opt.eachSizeW - 1);
  const randomY = getRandom(0, opt.eachSizeH - 1);
  return {
    left: randomX * blockW + 10,
    top: randomY * blockH + 10,
  };
}

//随机获得一个小方块类型
function getBlockType() {
  const randomNum = getRandom(0, allBlockTypesObj.length);
  //判断如果只剩一个就需要移除
  if (allBlockTypesObj[randomNum].num <= 1) {
    return removeType(allBlockTypesObj[randomNum].type)[0];
  } else {
    allBlockTypesObj[randomNum].num--;
  }
  return allBlockTypesObj[randomNum];
}

//在所有类型里面移除该项
function removeType(type) {
  for (let i = 0; i < allBlockTypesObj.length; i++) {
    if (allBlockTypesObj[i].type === type) {
      return allBlockTypesObj.splice(i, 1);
    }
  }
}

//判断是否该小方块能不能点
function clickEnabled(position, blockFlag) {
  //先拿到可能盖住该小方块的其他小方块信息
  const afterThisBlockPositionInfo =
    allBlockInfo.blockPosition.slice(blockFlag);
  // console.log(flag);
  if (!hasAboveBlock(position, afterThisBlockPositionInfo)) {
    // 该小方块上方有其他小方块 不能点
    // console.log('可以点');
    return true;
  } else {
    //没有其他小方块 可以点
    // console.log('不可以点');
    return false;
  }
}

// 判断一个小方块上方是否有其他小方块
function hasAboveBlock(position, afterThisBlockPositionInfo) {
  const blockLeft = position.left - blockW * 2;
  const blockRight = position.left + blockW * 2;
  const blockTop = position.top - blockH * 2;
  const blockBottom = position.top + blockH * 2;
  const flag = afterThisBlockPositionInfo.find((item) => {
    return (
      item.left > blockLeft &&
      item.left < blockRight &&
      item.top > blockTop &&
      item.top < blockBottom
    );
  });
  return flag ? true : false;
}

//提示用户
function handlePromptUser() {
  eventBus.$emit('promptUser');
}

const newHandlePromptUser = debounce(handlePromptUser, 5000);
