import Vue from 'vue';
import App from './App.vue';
import {
  Button,
  Icon,
  Switch,
  Slider,
  Tooltip,
  Message,
  MessageBox,
} from 'element-ui';

Vue.use(Button);
Vue.use(Icon);
Vue.use(Switch);
Vue.use(Slider);
Vue.use(Tooltip);
Vue.prototype.$message = Message;
Vue.prototype.$messageBox = MessageBox;

import 'normalize.css/normalize.css'; // 全局初始化样式
import './styles/common.css'; //全局样式

Vue.config.productionTip = false;

import './eventBus.js'; // 事件总线

import store from './store';

new Vue({
  store,
  render: (h) => h(App),
}).$mount('#app');
