import Vuex from 'vuex';
import Vue from 'vue';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: true,
  state: {
    userSettings: {
      modeValue: true,
      cardNumValue: 0,
      volumeValue: 6,
      cardTypeValue: true,
      removeNumValue: 3,
      cardTypeNumValue: 11,
      // 游戏是否结束
    },
    isGameOver: true,
    loadingStatus: 'start',
  },
  getters: {
    userSettings(state) {
      return state.userSettings;
    },
    isGameOver(state) {
      return state.isGameOver;
    },
    loadingStatus(state) {
      return state.loadingStatus;
    },
  },
  mutations: {
    SET_USER_SETTINGS(state, payload) {
      state.userSettings[payload[0]] = payload[1];
    },
    RESET_USER_SETTINGS(state) {
      state.userSettings = {
        modeValue: false,
        cardNumValue: 0,
        volumeValue: 6,
        cardTypeValue: true,
        removeNumValue: 3,
        cardTypeNumValue: 11,
      };
    },
    CHANGEGAMESTATUS(state, payload) {
      state.isGameOver = payload;
    },
    SETLOADINGSTATUS(state, payload) {
      state.loadingStatus = payload;
    },
  },
});
