<template>
  <div class="tab-bar-container">
    <Icon v-for="item in blockTypesArr" :key="item.id" :type="item.type"></Icon>
  </div>
</template>

<script>
import Icon from '@/components/Icon';
export default {
  data() {
    return {
      blockTypesArr: [],
      currentSelected: '',
    };
  },
  components: {
    Icon,
  },
  mounted() {
    this.$bus.$on('blockClick', ({ type }) => {
      const id = Math.random().toString().slice(2, 10);
      const info = {
        id,
        type,
      };
      this.currentSelected = type;
      this.blockTypesArr.push(info);
      this.checkGame();
    });
  },
  methods: {
    checkGame() {
      this.checkBlockType();
      if (this.blockTypesArr.length >= 7) {
        this.$store.commit('CHANGEGAMESTATUS', true);
        this.$bus.$emit('gameOver');
        this.$messageBox
          .confirm(
            '这游戏难度太高了不适合您,是否厚着脸皮继续?',
            '这么简单都被你玩没了!!!',
            {
              confirmButtonText: '忍气吞声,重开一局',
              cancelButtonText: '忍不了!',
              type: 'warning',
            }
          )
          .then(() => {
            history.go(0);
          })
          .catch(() => {
            window.open('https://wwww.4399.com', '_blank');
            history.go(0);
          });
      }
    },
    checkBlockType() {
      const result = this.blockTypesArr.filter(
        (item) => item.type === this.currentSelected
      );
      if (result.length >= 3) {
        this.blockTypesArr = this.blockTypesArr.filter(
          (item) => item.type !== this.currentSelected
        );
      }
    },
  },
};
</script>

