<template>
  <div id="app">
    <el-button type="primary" @click="handleStartGame">{{
      showStatus
    }}</el-button>
    <div class="game-main" ref="main"></div>
    <TabBar></TabBar>
  </div>
</template>

<script>
import createBlock from './utils/createBlocks.js';
import TabBar from '@/components/TabBar';
import { mapGetters } from 'vuex';
export default {
  components: {
    TabBar,
  },
  computed: {
    ...mapGetters(['loadingStatus']),
    showStatus() {
      if (this.loadingStatus === 'start') {
        return '开始游戏';
      } else if (this.loadingStatus === 'loading') {
        return '加载方块...';
      } else {
        return '游戏中...';
      }
    },
  },
  mounted() {
    this.$bus.$on('blockClick', ({ blockNum }) => {
      if (blockNum.length === 1) {
        this.$messageBox.alert(
          '你真是一个万中挑一的奇才,恭喜你通关了♥️～～～',
          '通关',
          {
            confirmButtonText: '确定',
            callback: (action) => {
              if (action === 'cancel') {
                window.open('https://wwww.4399.com', '_blank');
                history.go(0);
              } else {
                history.go(0);
              }
            },
          }
        );
        this.$store.commit('CHANGEGAMESTATUS', true);
        this.$store.commit('SETLOADINGSTATUS', 'start');
      }
    });
    this.$bus.$on('promptUser', () => {
      this.$message({
        showClose: true,
        message: '你在干嘛,哎呦～～～',
        type: 'warning',
        duration: 3000,
      });
    });
  },
  methods: {
    handleStartGame() {
      // console.log('游戏开始喽！！！');
      this.$message({
        message: '鸡汤来咯～～～',
        type: 'success',
        duration: 500,
        offset: 10,
      });
      this.$store.commit('CHANGEGAMESTATUS', false);
      createBlock(this.$refs.main, this.onCreateEnd);
    },
    onCreateEnd() {
      // console.log('小方格创建完成');
    },
  },
};
</script>

<style lang="less" scoped>
#app {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .game-main {
    height: 422px;
    width: 422px;
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    position: relative;
  }
}
</style>
